import React from "react";
import { useEffect, useState } from "react";
import styled, { createGlobalStyle } from "styled-components";
import $ from "jquery";
import { useWindowSize } from "./WindowSize";
const GlobalStyle = createGlobalStyle`
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif;
}
body{
    background: #ffffff;

}
a{
    text-decoration: none;
    color: inherit;
}
 h1{
    font-weight: 800;
    word-break: break-word;
}
p{
     word-break: break-word;
} 
textarea, input { outline: none !important; }

.load-preview
{
    width: 100%;
    
    position: fixed;
    top: 0;
    left:0;
    overflow: hidden;
    z-index: 888;
    
}


.load-image
{
   width: 100%;
   height: 100%;
   background-size: contain;
   background-position: center;
   transform: scale(1);
   transition: 5s  transform ease-in-out;


}


.enter-btn
{
    position: absolute;
    bottom: 10%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    width: 100%;
    
}
.btn-name
{
    
    border: 1px solid #fff;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
}
.btn-name p
{
    color: white;
   
}

/*contacts*/
.first-slide,.second-slide,.third-slide
{
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    background: white;
  
    
    
}
.first-slide
{
  opacity:0;
 
}
.seen
{
  opacity: 1;
}
.contacts-slide
{
    width: 100vw;
    height: 85%;
    overflow: hidden;
    display: flex;
    flex-shrink: 0;
    justify-content: center;
    

}
.contacts-information
{
    width: 100%;
    height: auto;
    max-height: 95%;
    
    flex-shrink: 0;
    
    overflow: hidden scroll;
    
    
   
    margin-top: auto;
    margin-bottom: auto;

}

.profile
{
   
    border-radius: 50%;
    background: green;
    overflow: hidden;
}
img
{
    width: 100%;
    height: 100%;
}

.first-name , .last-name
{

    width: 85%;
    text-align: center;
}
.tag
{
    
    letter-spacing: 0.1em;
     font-weight: 900;
    
    
    width: 85%;
}
.social-media
{
    width: auto;
    
    
        display: flex;
        flex-shrink: 0;
        justify-content: center;
        -webkit-box-align: center;
        align-items: center;
       
}
.social-media a
{
    
    height: auto;
}
.social-media img
{
    object-fit: contain;
    width: 100%;
    height: 100%;

}

.contact-container
{
    
    width: 70%;
    display: flex;
    flex-shrink: 0;
    -webkit-box-align: center;
    align-items: center;
   
    
}
.contact-container img
{
    
    filter:drop-shadow(-6px 8px 15px rgba(202, 128, 115, 0.2)); 


}

.contact-container p
{
    color: #523630;
   
    width: 80%;
   
}
.bottom-img
{
    width: 100%;
    
    position: absolute;
    bottom: 0;
}

.save-btn
{
    
    position: absolute;
    bottom: 2%;
    right: 40%;
    z-index: 5;
    display: flex;
    flex-shrink: 0;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.save-icon
{
    


    filter:drop-shadow(0px 5px 15px rgba(202, 128, 115, 0.25)); 
}
.share-btn
{
    position: absolute;
    top: 4%;
    right: 6%;
    z-index: 5;
    display: flex;
    flex-shrink: 0;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.layerblur-img
{
    position: absolute;
    top: 0%;
    right: 0%;
   
   

}
.share-icon
{
    

    filter:drop-shadow(0px 5px 15px rgba(202, 128, 115, 0.25)); 
}

.next-btn
{
    position: absolute;
    bottom: 8.5%;
    right: 6%;
    z-index: 5;
    
    
}
.next-icon
{
    
    filter:drop-shadow(0px 5px 15px rgba(202, 128, 115, 0.25))
}

/*second page*/
.second-slide
{
    background: url('slide2background.png') no-repeat;
    background-size: cover;
    background-position: center;
    z-index: -8;
   overflow: scroll;

}
.image-up
{
    
    width: 100%;
   
    overflow: hidden;
}
.image-down
{
   
    width: 100%;
   
    overflow: hidden;
    
}






.up-img, .down-img,.thirdImage
{
    transform: scale(1);
   transition: 5s  transform ease-in-out;
}

.scale
{
    transform: scale(1.2);
}



.quote-content
{
    
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    color: #3E3E3E;
}
.prev-btn
{
    z-index: 5;
}
.prev-icon
{
   
    filter: drop-shadow(0px 5px 15px rgba(202, 128, 115, 0.25));
}

.third-slide
{
    background: url('slide3background.png')no-repeat;
    background-size: cover;
    background-position: center;
    z-index: -12;

}
.review-section
{
   width: 100%;
   display: flex;
   flex-direction: column;
   align-items: center;
   
}
.review-section h1
{
    width: 100%;
    
    text-align:center;
   
}

/*share slide*/
 .share-slide
{
    position: fixed;
    width: 100%;
    height: 100%;
    overflow: hidden;
    background-color: #000000;
    mix-blend-mode: normal;

    z-index: 222;
    transform: translateX(-100%); 
    transition: transform 1s ease-out 0s;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
}
.share-wrapper
{
    width: 100%;
    height: 100%;
    position: relative;
    
}

.share-slide .close-icon
{
    position: absolute;
   
    top: 3%;
    right: 5%;
}
     
   
    
}
.share-slide-share-icons
{

    display: flex;
    flex-direction: column;
    align-items: center;
    flex-shrink: 0;
    position: fixed;
    top: 50%;
    left: 50%; 
    /* bring your own prefixes */
    transform: translate(-50%, -55%);
   
}
.share-headline 
{
   
    color: #1D3138;
   
    transform: translateX(-80%);
    transition:  transform 1s ease-out;

    
}
.scan-and-save-txt
{
    
    color: #1D3138;
  
    margin-top: 30%;
    transform: translateX(-80%);
    transition:  transform 1s ease-out;
}



.icon-grid
{
    display: grid;
    width: 100%;
    grid-template-columns: 11vw 11vw 11vw 11vw;
    
    
    justify-content: center;
    transform: translateX(-40%);
    transition:  transform 1s ease-out;
    
}
.icon-grid a
{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    filter: drop-shadow(-6px 8px 15px rgba(202,128,115,0.2));
}
.icon-grid img
{
    
}

.qr-code-wrapper
{
    width: 100%;
    height: 50vh;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
    color: #1D4B7A;
    align-items: center;
}


.open{
    transform: translateX(0%);
}
.closeAnim
{
    transition:  transform 0.5s ease-out;
    transform: rotateZ(180deg);
   
}

`;

const Travel = () => {
  const size = useWindowSize();

  const [darkMode, setDarkMode] = useState(true);

  const getSize = (width, heightPercentage) => {
    return `${Math.min(
      (width * size.width) / 100,
      (size.height * heightPercentage) / 100
    )}px`;
  };

  const getHeight = (heightPercentage) => {
    return `${(size.height * heightPercentage) / 100}px`;
  };

  useEffect(() => {
    $(".second-slide").fadeOut();
    $(".third-slide").fadeOut("fast");
    $(".load-image").removeClass("scale");
  }, []);

  $(document).ready(function () {
    setInterval(function () {
      $(".load-preview").fadeOut(500, "linear");
      $(".load-image").addClass("scale");
      $(".first-slide").addClass("seen");
    }, 5000);
  });

  /*var test = setInterval(function () {
    $(".load-preview").fadeOut(500, "linear");
    $(".load-image").addClass("scale");
  }, 5000);*/

  function enter() {
    $(".load-preview").fadeOut(1000, "linear");
    $(".load-image").addClass("scale");
    $(".first-slide").fadeIn(1000, "linear");
    $(".first-slide").addClass("seen");
  }
  function goToSecond() {
    $(".second-slide").fadeIn(1000, "linear");
    $(".first-slide").fadeOut(1000, "linear");
    $(".third-slide").fadeOut(1000, "linear");
    $(".up-img ").removeClass("scale");
    $(".down-img").removeClass("scale");
  }

  function goToFirst() {
    $(".second-slide").fadeOut(1000, "linear");
    $(".first-slide").fadeIn(1000, "linear");
    $(".third-slide").fadeOut(1000, "linear");
    $(".up-img").addClass("scale");
    $(".down-img").addClass("scale");
    $(".thirdImage").addClass("scale");
  }

  function goToThird() {
    $(".second-slide").fadeOut(1000, "linear");
    $(".third-slide").fadeIn(1000, "linear");
    $(".first-slide").fadeOut(1000, "linear");
    $(".up-img ").addClass("scale");
    $(".down-img").addClass("scale");
    $(".thirdImage").removeClass("scale");
  }

  function openSlide() {
    $(".share-slide").addClass("open");

    $(".close-icon").removeClass("closeAnim");
  }

  function closeSlide() {
    $(".share-slide").removeClass("open");
    $(".close-icon").addClass("closeAnim").delay(1000);
  }

  return (
    <div>
      <GlobalStyle />
      <div
        className="share-slide"
        id="share-slide"
        style={{
          background: darkMode
            ? "#000000"
            : "url('https://raw.githubusercontent.com/rohit-virkud/restro-card/main/Rectangle.png')",
        }}
      >
        <div
          className="share-wrapper"
          style={{
            background: darkMode ? "#FFFFFF" : "#FFFFFF",
          }}
        >
          <img
            src={
              darkMode
                ? "https://raw.githubusercontent.com/rohit-virkud/restro-card/main/close-icon-dark.png"
                : "https://raw.githubusercontent.com/rohit-virkud/restro-card/main/close-icon.png"
            }
            alt="close-icon"
            className="close-icon"
            onClick={(e) => {
              e.preventDefault();
              closeSlide();
            }}
            style={{
              width: getSize(5, 3),
              height: getSize(5, 3),
            }}
          />
          <div className="share-slide-share-icons">
            <h2
              className="share-headline open"
              style={{
                fontSize: getSize(5, 2.5),
                marginBottom: getSize(4, 3),
              }}
            >
              SHARE MY CARD
            </h2>
            <div
              className="icon-grid open"
              style={{
                gridGap: getSize(2, 1),
              }}
            >
              <a href="#!">
                <img
                  src="https://raw.githubusercontent.com/rohit-virkud/restro-card/main/sms.png"
                  alt="sms-img"
                  style={{
                    width: getSize(11, 5),
                    height: getSize(11, 5),
                  }}
                />
              </a>
              <a href="#!">
                <img
                  src="https://raw.githubusercontent.com/rohit-virkud/restro-card/main/cs.png"
                  alt="cs-img"
                  style={{
                    width: getSize(11, 5),
                    height: getSize(11, 5),
                  }}
                />
              </a>
              <a href="#!">
                <img
                  src="https://raw.githubusercontent.com/rohit-virkud/restro-card/main/whatsapp.png"
                  alt="whatsapp-img"
                  style={{
                    width: getSize(11, 5),
                    height: getSize(11, 5),
                  }}
                />
              </a>
              <a href="#!">
                <img
                  src="https://raw.githubusercontent.com/rohit-virkud/restro-card/main/dropbox 187.png"
                  alt="dropbox-img"
                  style={{
                    width: getSize(11, 5),
                    height: getSize(11, 5),
                  }}
                />
              </a>
              <a href="#!">
                <img
                  src="https://raw.githubusercontent.com/rohit-virkud/restro-card/main/fb9.png"
                  alt="fb-img"
                  style={{
                    width: getSize(11, 5),
                    height: getSize(11, 5),
                  }}
                />
              </a>
              <a href="#!">
                <img
                  src="https://raw.githubusercontent.com/rohit-virkud/restro-card/main/telegram 191.png"
                  alt="tel-img"
                  style={{
                    width: getSize(11, 5),
                    height: getSize(11, 5),
                  }}
                />
              </a>
              <a href="#!">
                <img
                  src="https://raw.githubusercontent.com/rohit-virkud/restro-card/main/twitter8.png"
                  alt="twit-img"
                  style={{
                    width: getSize(11, 5),
                    height: getSize(11, 5),
                  }}
                />
              </a>
              <a href="#!">
                <img
                  src="https://raw.githubusercontent.com/rohit-virkud/restro-card/main/linkedin3.png"
                  alt="linkedin-img"
                  style={{
                    width: getSize(11, 5),
                    height: getSize(11, 5),
                  }}
                />
              </a>
            </div>
            <h2
              className="scan-and-save-txt open"
              style={{
                fontSize: getSize(5, 2.5),
                marginBottom: getSize(4, 4),
              }}
            >
              SCAN AND SAVE
            </h2>
            <div className="qr-code-container open">
              <img
                src="https://raw.githubusercontent.com/rohit-virkud/restro-card/main/qr.png"
                alt="qr-img"
                style={{
                  width: getSize(30, 20),
                  height: getSize(30, 20),
                }}
              />
            </div>
          </div>
        </div>
      </div>
      <div
        className="load-preview"
        style={{
          height: "100vh",
        }}
      >
        <img
          src="https://raw.githubusercontent.com/rohit-virkud/travel/main/background.png"
          className="load-image scale"
          alt="preview-img "
        />
        <div className="enter-btn">
          <a
            href="#!"
            onClick={(e) => {
              e.preventDefault();
              enter();
            }}
            className="btn-name"
            style={{
              width: getSize(30, 15),
              height: getSize(30, 15),
            }}
          >
            <p
              style={{
                fontSize: getSize(4, 2.5),
              }}
            >
              ENTER
            </p>
          </a>
        </div>
      </div>

      <div className="first-slide" style={{ height: getHeight(100) }}>
        <div className="contacts-slide">
          <div className="contacts-information">
            <img
              src="https://raw.githubusercontent.com/rohit-virkud/travel/main/layerblur.png"
              alt="layerblur"
              className="layerblur-img"
              style={{
                width: getSize(70, 35),
                height: getSize(70, 35),
              }}
            />
            <div
              style={{
                width: "100%",
                display: "flex",
                justifyContent: "center",
              }}
            >
              <div
                className="profile"
                style={{
                  zIndex: "10",
                  width: getSize(30, 15),
                  height: getSize(30, 15),
                }}
              >
                <img
                  src="https://raw.githubusercontent.com/rohit-virkud/travel/main/profile.png"
                  alt="profile"
                  className="profile-img"
                />
              </div>
            </div>
            <div
              style={{
                width: "100%",
                display: "flex",
                justifyContent: "center",
              }}
            >
              <h1
                className="first-name"
                style={{
                  zIndex: "10",
                  marginTop: getSize(4, 2.5),
                  fontSize: getSize(6, 3),
                }}
              >
                Denis
              </h1>
            </div>
            <div
              style={{
                width: "100%",
                display: "flex",
                justifyContent: "center",
              }}
            >
              <h1
                className="last-name"
                style={{ zIndex: "10", fontSize: getSize(6, 3) }}
              >
                Brown
              </h1>
            </div>

            <div
              style={{
                width: "100%",
                display: "flex",
                justifyContent: "center",
              }}
            >
              <h3
                className="tag"
                style={{
                  zIndex: "10",
                  textAlign: "center",
                  color: "#616161",
                  fontSize: getSize(3, 1.5),
                  marginTop: getSize(2, 1),
                }}
              >
                Travel Agency
              </h3>
            </div>

            <div
              className="social-media"
              style={{
                marginBottom: getSize(5, 2),
                height: getSize(14, 7),
                marginTop: getSize(6, 3),
              }}
            >
              <a
                href="#!"
                style={{
                  width: getSize(10, 6),
                }}
              >
                <img
                  src="https://raw.githubusercontent.com/rohit-virkud/travel/main/facebook.png"
                  alt="fb"
                />
              </a>
              <a
                href="#!"
                style={{
                  width: getSize(10, 6),
                }}
              >
                <img
                  src="https://raw.githubusercontent.com/rohit-virkud/travel/main/instagram.png"
                  alt="fb"
                />
              </a>
              <a
                href="#!"
                style={{
                  width: getSize(10, 6),
                }}
              >
                <img
                  src="https://raw.githubusercontent.com/rohit-virkud/travel/main/youtube.png"
                  alt="fb"
                />
              </a>
              <a
                href="#!"
                style={{
                  width: getSize(10, 6),
                }}
              >
                <img
                  src="https://raw.githubusercontent.com/rohit-virkud/travel/main/telegram.png"
                  alt="fb"
                />
              </a>
              <a
                href="#!"
                style={{
                  width: getSize(10, 6),
                }}
              >
                <img
                  src="https://raw.githubusercontent.com/rohit-virkud/travel/main/twitter.png"
                  alt="fb"
                />
              </a>
              <a
                href="#!"
                style={{
                  width: getSize(10, 6),
                }}
              >
                <img
                  src="https://raw.githubusercontent.com/rohit-virkud/travel/main/tiktok.png"
                  alt="fb"
                />
              </a>
              <a
                href="#!"
                style={{
                  width: getSize(10, 6),
                }}
              >
                <img
                  src="https://raw.githubusercontent.com/rohit-virkud/travel/main/linkedin.png"
                  alt="fb"
                />
              </a>
            </div>

            <div
              style={{
                display: "flex",
                flexDirection: "Column",
                width: "100%",
                alignItems: "center",
                flexShrink: "0",
              }}
            >
              <a
                href="#!"
                className="contact-container"
                style={{
                  marginTop: getSize(3, 1.5),
                  marginBottom: getSize(3.25, 1.5),
                  fontSize: getSize(6, 3),
                  /*marginLeft: getSize(15, 7.5),*/
                  marginLeft: getSize(7, 3.5),
                }}
              >
                <img
                  src="https://raw.githubusercontent.com/rohit-virkud/travel/main/phone-icon.png"
                  alt="contact"
                  style={{ width: getSize(9, 4.5), height: getSize(9, 4.5) }}
                />

                <p
                  style={{
                    marginLeft: getSize(6, 3),
                    fontSize: getSize(3.5, 2),
                  }}
                >
                  +380 98 996 5879
                </p>
              </a>

              <a
                href="#!"
                className="contact-container"
                style={{
                  marginTop: getSize(3, 1.5),
                  marginBottom: getSize(3.25, 1.5),
                  fontSize: getSize(6, 3),
                  marginLeft: getSize(7, 3.5),
                }}
              >
                <img
                  src="https://raw.githubusercontent.com/rohit-virkud/travel/main/home-icon.png"
                  alt="contact"
                  style={{ width: getSize(9, 4.5), height: getSize(9, 4.5) }}
                />
                <p
                  style={{
                    marginLeft: getSize(6, 3),
                    fontSize: getSize(3.5, 2),
                  }}
                >
                  +380 98 996 5879
                </p>
              </a>

              <a
                href="#!"
                className="contact-container"
                style={{
                  marginTop: getSize(3, 1.5),
                  marginBottom: getSize(3.25, 1.5),
                  fontSize: getSize(6, 3),
                  marginLeft: getSize(7, 3.5),
                }}
              >
                <img
                  src="https://raw.githubusercontent.com/rohit-virkud/travel/main/email-icon.png"
                  alt="contact"
                  style={{ width: getSize(9, 4.5), height: getSize(9, 4.5) }}
                />
                <p
                  style={{
                    marginLeft: getSize(6, 3),
                    fontSize: getSize(3.5, 2),
                  }}
                >
                  share@artcoolcards.com
                </p>
              </a>

              <a
                href="#!"
                className="contact-container"
                style={{
                  marginTop: getSize(3, 1.5),
                  marginBottom: getSize(3.25, 1.5),
                  fontSize: getSize(6, 3),
                  marginLeft: getSize(7, 3.5),
                }}
              >
                <img
                  src="https://raw.githubusercontent.com/rohit-virkud/travel/main/location-icon.png"
                  alt="contact"
                  style={{ width: getSize(9, 4.5), height: getSize(9, 4.5) }}
                />
                <p
                  style={{
                    marginLeft: getSize(6, 3),
                    fontSize: getSize(3.5, 2),
                  }}
                >
                  Dimokratias Avenue, Paphos 8066, Cyprus
                </p>
              </a>

              <a
                href="#!"
                className="contact-container"
                style={{
                  marginTop: getSize(3, 1.5),
                  marginBottom: getSize(3.25, 1.5),
                  fontSize: getSize(6, 3),
                  marginLeft: getSize(7, 3.5),
                }}
              >
                <img
                  src="https://raw.githubusercontent.com/rohit-virkud/travel/main/web-icon.png"
                  alt="contact"
                  style={{ width: getSize(9, 4.5), height: getSize(9, 4.5) }}
                />
                <p
                  style={{
                    marginLeft: getSize(6, 3),
                    fontSize: getSize(3.5, 2),
                  }}
                >
                  www.artcoolcards.com
                </p>
              </a>
            </div>
            <a href="#!" className="save-btn">
              <img
                src="https://raw.githubusercontent.com/rohit-virkud/travel/main/save-icon.png"
                alt="save-btn"
                className="save-icon"
                style={{
                  width: getSize(14, 9),
                  height: getSize(14, 9),
                }}
              />
              <p
                style={{
                  marginTop: getSize(2, 1),
                  color: "#8C8C8C",
                  letterSpacing: "0.05em",
                  fontWeight: "700",
                  fontSize: getSize(2.8, 1.2),
                }}
              >
                SAVE CONTACTS
              </p>
            </a>

            <div className="next-btn">
              <img
                src="https://raw.githubusercontent.com/rohit-virkud/travel/main/next.png"
                alt="next"
                className="next-icon"
                onClick={(e) => {
                  e.preventDefault();
                  goToSecond();
                }}
                style={{
                  width: getSize(10, 5),
                  height: getSize(10, 5),
                }}
              />
            </div>

            <a
              href="#!"
              onClick={(e) => {
                e.preventDefault();
                openSlide();
              }}
              className="share-btn"
            >
              <img
                src="https://raw.githubusercontent.com/rohit-virkud/travel/main/share-icon.png"
                alt="save-btn"
                className="share-icon"
                style={{
                  width: getSize(10, 5),
                  height: getSize(10, 5),
                }}
              />
              <p
                style={{
                  marginTop: getSize(2, 1),
                  color: "#8C8C8C",
                  letterSpacing: "0.05em",
                  fontWeight: "700",
                  fontSize: getSize(2.8, 1.2),
                  width: getSize(10, 5),
                  height: getSize(10, 5),
                }}
              >
                SHARE
              </p>
            </a>

            <img
              src="https://raw.githubusercontent.com/rohit-virkud/travel/main/bottom.png"
              alt="bottom"
              className="bottom-img"
              style={{
                height: getSize(23, 13),
              }}
            />
          </div>
        </div>
      </div>
      <div
        className="second-slide"
        style={{
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
          flexShrink: "0",
          minHeight: getHeight(100),
        }}
      >
        <div
          className="image-up"
          style={{
            height: getHeight(35),
          }}
        >
          <img
            src="https://raw.githubusercontent.com/rohit-virkud/travel/main/slide02upper.png"
            alt="image01"
            className="up-img scale"
          />
        </div>
        <div className="quote-content" style={{ minHeight: getHeight(30) }}>
          <div className="prev-btn">
            <img
              src="https://raw.githubusercontent.com/rohit-virkud/travel/main/prev.png"
              alt="prev"
              className="prev-icon"
              onClick={(e) => {
                e.preventDefault();
                goToFirst();
              }}
              style={{
                width: getSize(10, 5),
                height: getSize(10, 5),
              }}
            />
          </div>

          <div
            style={{
              width: getSize(60, 40),
              textAlign: "center",
              marginTop: getSize(10, 5),
              marginBottom: getSize(10, 5),
              marginLeft: getSize(5, 2.5),
              marginRight: getSize(5, 2.5),
            }}
          >
            <h1
              style={{
                width: "100%",
                textAlign: "center",
                fontSize: getSize(5, 2.5),
              }}
            >
              THERE IS ALWAYS ROOM FOR A STORY THAT CAN TRANPORT PEOPLE TO
              ANOTHER PLACE
            </h1>
          </div>

          <div className="next-btn" style={{ position: "static" }}>
            <img
              src="https://raw.githubusercontent.com/rohit-virkud/travel/main/next.png"
              alt="next"
              className="next-icon"
              onClick={(e) => {
                e.preventDefault();
                goToThird();
              }}
              style={{
                width: getSize(10, 5),
                height: getSize(10, 5),
              }}
            />
          </div>
        </div>
        <div className="image-down">
          <img
            src="https://raw.githubusercontent.com/rohit-virkud/travel/main/slide02bottom.png"
            alt="image02"
            className="down-img scale"
            style={{
              height: getHeight(35),
            }}
          />
        </div>
      </div>

      <div
        className="third-slide"
        style={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          flexShrink: "0",
          minHeight: "100vh",
          overflow: "scroll",
        }}
      >
        <div
          className="container"
          style={{
            width: "85%",

            /*minHeight: "100%",*/
            marginTop: "auto",
            marginBottom: "auto",
            paddingBottom: getSize(10, 5),
          }}
        >
          <div
            className="prev-btn"
            style={{ position: "absolute", top: "1.5%", left: "4%" }}
          >
            <img
              src="https://raw.githubusercontent.com/rohit-virkud/travel/main/prev.png"
              alt="prev"
              className="prev-icon"
              onClick={(e) => {
                e.preventDefault();
                goToSecond();
              }}
              style={{
                width: getSize(10, 5),
                height: getSize(10, 5),
              }}
            />
          </div>
          <div
            className="third-image-container"
            style={{
              width: "100%",
              height: "55%",
              marginTop: getSize(8, 4),
              overflow: "hidden",
            }}
          >
            <img
              src="https://raw.githubusercontent.com/rohit-virkud/travel/main/thirdImg.png"
              alt="thirdImage"
              style={{
                backgroundSize: "contain",
                backgroundPosition: "center",
              }}
              className="thirdImage scale"
            />
          </div>

          <h1
            style={{
              marginTop: getSize(5, 3),
              textAlign: "center",
              fontSize: getSize(6, 3),
              fontWeight: "900",
            }}
          >
            About Us
          </h1>
          <p
            style={{
              marginTop: getSize(4, 2),
              textAlign: "center",
              fontSize: getSize(3.5, 2.5),
              fontWeight: "400",
              color: "#616161",
              marginBottom: getSize(4, 2),
            }}
          >
            Lorem Ipsum is simply dummy text of the printing and typesetting
            industry. Lorem Ipsum has been the industry's standard dummy text
            ever since the 1500s, when an unknown printer took a galley of type
            and scrambled it to make a type specimen book. Lorem Ipsum has been
            the industry's standard dummy text ever since the 1500s, when an
            unknown printer took a galley of type and scrambled it to make a
            type specimen book.{" "}
          </p>

          <div className="review-section">
            <h1
              style={{
                fontSize: getSize(6, 3),
                marginTop: getSize(4, 3),
              }}
            >
              Our Reviews
            </h1>
            <div
              className="reviews"
              style={{
                marginTop: getSize(4, 2),
                marginBottom: getSize(4, 2),
                width: "30%",
                display: "flex",
                justifyContent: "space-around",
                alignItems: "center",
              }}
            >
              <a href="#!">
                <img
                  src="https://raw.githubusercontent.com/rohit-virkud/travel/main/Tripadvisor.png"
                  alt="img"
                  style={{ width: getSize(9, 6), height: getSize(9, 6) }}
                />
              </a>
              <a href="#!">
                <img
                  src="https://raw.githubusercontent.com/rohit-virkud/travel/main/booking.png"
                  alt="img"
                  style={{ width: getSize(9, 6), height: getSize(9, 6) }}
                />
              </a>
            </div>
          </div>

          <h1
            style={{
              width: "80%",
              fontSize: getSize(6, 3),
              fontWeight: "900",
              marginTop: getSize(9, 5),
            }}
          >
            Subscribe
          </h1>
          <form
            method="post"
            className="form"
            style={{
              marginTop: getSize(4, 2),
              display: "flex",
              flexDirection: "column",
              flexShrink: "0",
              /* marginBottom: getSize(10, 5),*/
            }}
          >
            <input
              type="name"
              name="name"
              placeholder="Enter Your Name"
              className="input"
              style={{
                fontWeight: "400",
                paddingTop: getSize(2.5, 1.5),
                paddingBottom: getSize(2.5, 1.5),
                paddingLeft: getSize(3, 2),
                paddingRight: getSize(3, 2),
                fontSize: getSize(3.405, 1.5),
                lineHeight: getSize(5.085, 3),
                borderRadius: getSize(10, 5),
                border: "1px solid black",
              }}
            />
            <input
              type="email"
              name="email"
              placeholder="Enter Your Email"
              className="input"
              style={{
                fontWeight: "400",
                marginTop: getSize(4, 2),
                paddingTop: getSize(2.5, 1.5),
                paddingBottom: getSize(2.5, 1.5),
                paddingLeft: getSize(3, 2),
                paddingRight: getSize(3, 2),
                fontSize: getSize(3.405, 1.5),
                lineHeight: getSize(5.085, 2.5),
                borderRadius: getSize(10, 5),
                border: "1px solid black",
              }}
            />
            <input
              type="submit"
              name="submit"
              className="input primary-btn"
              value="SUBMIT"
              style={{
                width: "30%",
                backgroundColor: "rgb(71, 156, 220)",
                color: "rgb(255, 255, 255)",
                border: "none",
                boxShadow: "rgba(86, 84, 84, 0.2) 0px 4px 20px",
                paddingTop: getSize(3, 1.5),
                paddingBottom: getSize(3, 1.5),

                fontSize: getSize(3.405, 1.5),
                lineHeight: getSize(5.085, 2.5),
                borderRadius: getSize(10, 5),
                marginTop: getSize(4, 2),
              }}
            />
          </form>
        </div>
      </div>
    </div>
  );
};

export default Travel;
